import React, { useEffect, useState } from "react";
import {
  Table,
  Input,
  InputNumber,
  Popconfirm,
  Form,
  Select,
  Button,
  Spin,
} from "antd";
import "./tableEdit.css";
import { useSelector, useDispatch } from "react-redux";
import {
  actHideLoading,
  actShowLoading,
} from "../../../../actions/actionLoading";
import axios from "../../../../util/api";
import {
  convertNumber,
  openNotificationWithIcon,
  orderType,
} from "../../../../util/help";
import { actDropdownTypeCosts } from "../../../../actions/actionDropdown";

const { Option } = Select;

const CreateItemYeuCauThanhToan = ({ job_d, id, getDataDetail }) => {
  const [form] = Form.useForm();
  const [state, setState] = useState({ data: [] });
  const [editingKey, setEditingKey] = useState("");
  const [changeSelect, setChangSelect] = useState("select");
  const [changeSelectCurrency, setChangeSelectCurrency] = useState("select");

  const isLoading = useSelector((state) => state.isLoading);
  const dropdown = useSelector((state) => state.dropdown);

  const dispatch = useDispatch();
  const showLoading = () => dispatch(actShowLoading());
  const hideLoading = () => dispatch(actHideLoading());
  const typeCosts = () => dispatch(actDropdownTypeCosts());

  const EditableCell = ({
    editing,
    dataIndex,
    title,
    inputType,
    record,
    index,
    children,
    ...restProps
  }) => {
    const inputNode =
      inputType === "selectType" ? (
        <Select>
          <Option key="Our Company Pay">Our Company Pay</Option>
          <Option key="Pay In Advance">Pay In Advance</Option>
          <Option key="Trucking Fee">Trucking Fee</Option>
          <Option key="Deposit fee">Cược Cont</Option>
          <Option key="Deposit fix fee">Cược Sửa Chữa Cont</Option>
          <Option key="Refund Customer">Refund Khách Hàng</Option>
          <Option key="Refund Carriers">Refund Hãng Tàu</Option>
          <Option key="Refund Agent">Refund Đại Lý</Option>
        </Select>
      ) : inputType === "selectDes" ? (
        changeSelect === "Thêm" ? (
          <Input />
        ) : (
          <Select
            showSearch
            optionFilterProp="children"
            filterOption={(input, option) =>
              option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
            }
            onSelect={(value) => setChangSelect(value)}
          >
            <Option key="Thêm">Thêm</Option>
            {dropdown.costs.map((item) => {
              return (
                <Option key={item.DESCRIPTION_CODE}>
                  {item.DESCRIPTION_NAME_VN}
                </Option>
              );
            })}
          </Select>
        )
      ) : inputType === "selectTax" ? (
        <Select onChange={getDataField}>
          <Option key="0">0</Option>
          <Option key="10">10</Option>
        </Select>
      ) : inputType === "number" ? (
        <InputNumber
          formatter={(value) =>
            `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
          }
          parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
          onChange={getDataField}
        />
      ) : inputType === "selectDor" ? (
        <Select
          onSelect={(value) => {
            setChangeSelectCurrency(value);
          }}
        >
          <Option key="VND">VND</Option>
          <Option key="USD">USD</Option>
        </Select>
      ) : inputType === "dorAmt" ? (
        changeSelectCurrency === "USD" ? (
          <InputNumber
            formatter={(value) =>
              `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
            }
            parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
            onChange={getDataField}
          />
        ) : (
          <InputNumber disabled />
        )
      ) : inputType === "dorRate" ? (
        changeSelectCurrency === "USD" ? (
          <InputNumber
            onChange={getDataField}
            formatter={(value) =>
              `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
            }
            parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
          />
        ) : (
          <InputNumber disabled />
        )
      ) : (
        <Input />
      );
    return (
      <td {...restProps}>
        {editing ? (
          <Form.Item
            name={dataIndex}
            style={{
              margin: 0,
            }}
          >
            {inputNode}
          </Form.Item>
        ) : (
          children
        )}
      </td>
    );
  };

  useEffect(() => {
    typeCosts();
    var newData = job_d;
    for (var i = 0; i < newData.length; i++) {
      newData[i].key = i;
    }
    setState({ ...state, data: newData });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [job_d]);

  const isEditing = (record) => record.key === editingKey;

  const edit = (record) => {
    form.setFieldsValue({
      DEB_TYPE: "",
      DESCRIPTION: "",
      DESCRIPTION_NAME_VN: "",
      INV_NO: "",
      UNIT: "",
      DOR_NO: "VND",
      DOR_RATE: 0,
      QUANTITY: 0,
      PRICE: 0,
      TAX_NOTE: 0,
      DOR_AMT: 0,
      TAX_AMT: 0,
      TOTAL_AMT: 0,
      ...record,
    });
    setEditingKey(record.key);
  };

  const getDataField = (record) => {
    var tong = 0;
    var dorNo = form.getFieldsValue().DOR_NO;
    var qty = form.getFieldsValue().QUANTITY;
    var price = form.getFieldsValue().PRICE;
    var taxNote = form.getFieldsValue().TAX_NOTE;
    var priceDor = form.getFieldsValue().DOR_AMT;
    var rate = form.getFieldsValue().DOR_RATE;
    if (dorNo === "VND") {
      tong = qty * price + qty * price * (taxNote / 100);
      form.setFieldsValue({
        TOTAL_AMT: tong,
        ...record,
      });
    } else {
      tong =
        qty * (priceDor * rate) + qty * (priceDor * rate) * (taxNote / 100);
      form.setFieldsValue({
        PRICE: priceDor * rate,
        TOTAL_AMT: tong,
        ...record,
      });
    }
  };

  const cancel = () => {
    setChangSelect("select");
    setEditingKey("");
  };

  const save = async (key) => {
    try {
      const row = await form.validateFields();
      const newData = [...state.data];
      const index = newData.findIndex((item) => key === item.key);

      if (index > -1) {
        const item = newData[index];
        newData.splice(index, 1, { ...item, ...row });
        if (newData[index].DESCRIPTION) {
          const typeTest = dropdown.costs.find(
            (i) => i.DESCRIPTION_CODE === newData[index].DESCRIPTION_NAME_VN
          );
          if (typeTest) {
            newData[index].DESCRIPTION = typeTest.DESCRIPTION_CODE;
          } else {
            newData[index].DESCRIPTION = newData[index].DESCRIPTION_NAME_VN;
          }
        } else {
          newData[index].DESCRIPTION = newData[index].DESCRIPTION_NAME_VN;
        }
        newData[index].JOB_NO = id;
        newData[index].INV_YN = "N";
        newData[index].TAX_AMT =
          newData[index].TOTAL_AMT -
          newData[index].PRICE * newData[index].QUANTITY;
        showLoading();
        if (newData[index].SER_NO) {
          newData[index].MODIFY_USER = localStorage.getItem("USER_NO");

          axios
            .post("payment/debit-note/edit-d", newData[index])
            .then((res) => {
              if (res.status === 200) {
                if (res.data.success === true) {
                  getDataDetail(id);
                  hideLoading();
                  openNotificationWithIcon("success", "Chỉnh Sửa Thành Công");
                }
              }
            })
            .catch((err) => console.log(err));
        } else {
          newData[index].BRANCH_ID = localStorage.getItem("BRANCH_ID");
          newData[index].INPUT_USER = localStorage.getItem("USER_NO");

          axios
            .post("payment/debit-note/add-d", newData[index])
            .then((res) => {
              if (res.status === 200) {
                if (res.data.success === true) {
                  getDataDetail(id);
                  hideLoading();
                  openNotificationWithIcon("success", "Tạo Thành Công");
                }
              }
            })
            .catch((err) => console.log(err));
        }

        setState({ ...state, data: newData });
        setEditingKey("");
      } else {
        newData.push(row);
        setState({ ...state, data: newData });
        setEditingKey("");
      }
    } catch (errInfo) {
      console.log("Validate Failed:", errInfo);
    }
  };

  const Delete = async (key) => {
    try {
      const row = await form.validateFields();
      const newData = [...state.data];
      const index = newData.findIndex((item) => key.key === item.key);

      if (index > -1) {
        const params = {
          JOB_NO: id,
          SER_NO: newData[index].SER_NO,
        };
        showLoading();
        axios
          .post("payment/debit-note/remove-d", params)
          .then((res) => {
            if (res.status === 200) {
              if (res.data.success === true) {
                newData.splice(index, 1);
                setState({ ...state, data: newData });
                setEditingKey("");
                hideLoading();
                openNotificationWithIcon("success", `Bạn đã xoá thành công`);
              }
            }
          })
          .catch((err) => console.log(err));
      }
    } catch (errInfo) {
      console.log("Validate Failed:", errInfo);
    }
  };


  const handleAdd = async () => {
    const row = await form.validateFields();
    const newData = [...state.data];
    row.key = newData.length;
    newData.push(row);
    setEditingKey("");
    setState({ ...state, data: newData });
  };

  const columns = [
    {
      title: "Loại",
      dataIndex: "DEB_TYPE",
      width: 250,
      editable: true,
    },
    {
      title: "Invoice No",
      dataIndex: "INV_NO",
      width: 150,
      editable: true,
    },
    {
      title: "Mô tả",
      dataIndex: "DESCRIPTION_NAME_VN",
      width: 250,
      editable: true,
    },
    {
      title: "UNIT",
      dataIndex: "UNIT",
      width: 150,
      editable: true,
    },
    {
      title: "Currency",
      dataIndex: "DOR_NO",
      width: 150,
      editable: true,
    },
    {
      title: "Price",
      dataIndex: "DOR_AMT",
      width: 150,
      editable: true,
      render: (text) => convertNumber(parseFloat(text)),
    },
    {
      title: "Rate",
      dataIndex: "DOR_RATE",
      width: 150,
      editable: true,
      render: (text) => convertNumber(parseFloat(text)),
    },
    {
      title: "QTY",
      dataIndex: "QUANTITY",
      width: 150,
      editable: true,
      render: (text) => convertNumber(parseFloat(text)),
    },
    {
      title: "Price (VND)",
      dataIndex: "PRICE",
      width: 150,
      editable: true,
      render: (text) => convertNumber(parseFloat(text)),
    },
    {
      title: "Tax",
      dataIndex: "TAX_NOTE",
      width: 100,
      editable: true,
      render: (text) => convertNumber(parseFloat(text)),
    },
    {
      title: "Total",
      dataIndex: "TOTAL_AMT",
      width: 150,
      editable: true,
      render: (text) => convertNumber(parseFloat(text)),
    },
    {
      title: "Action",
      fixed: "right",
      width: 150,
      dataIndex: "operation",
      render: (_, record) => {
        const editable = isEditing(record);
        return editable ? (
          <span>
            <Button
              accessKey="s"
              type="link"
              onClick={() => save(record.key)}
              style={{
                marginRight: 8,
              }}
            >
              Save
            </Button>
            <Popconfirm title="Sure to cancel?" onConfirm={cancel}>
              <Button type="link">Cancel</Button>
            </Popconfirm>
          </span>
        ) : (
          <>
            <Button type="link" accessKey="e" onClick={() => edit(record)}>
              Edit
            </Button>

            <Popconfirm
              title="Bạn có chắn chắn xóa?"
              onConfirm={() => Delete(record)}
            >
              <Button type="link">Delete</Button>
            </Popconfirm>
          </>
        );
      },
    },
  ];
  const mergedColumns = columns.map((col) => {
    if (!col.editable) {
      return col;
    }

    return {
      ...col,
      onCell: (record) => ({
        record,
        inputType:
          col.dataIndex === "DEB_TYPE"
            ? "selectType"
            : col.dataIndex === "DESCRIPTION_NAME_VN"
            ? "selectDes"
            : col.dataIndex === "DOR_NO"
            ? "selectDor"
            : col.dataIndex === "TAX_NOTE"
            ? "selectTax"
            : (col.dataIndex === "UNIT") | (col.dataIndex === "INV_NO")
            ? "text"
            : col.dataIndex === "DOR_AMT"
            ? "dorAmt"
            : col.dataIndex === "DOR_RATE"
            ? "dorRate"
            : "number",
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });
  return (
    <Form form={form} component={false}>
      <Button
        accessKey="n"
        onClick={handleAdd}
        type="primary"
        style={{ marginBottom: 16 }}
      >
        Add a row
      </Button>
      <Spin size="large" spinning={isLoading}>
        <Table
          components={{
            body: {
              cell: EditableCell,
            },
          }}
          bordered
          dataSource={state.data}
          columns={mergedColumns}
          // rowClassName="editable-row"
          pagination={false}
          scroll={{ x: 2200, y: 250 }}
        />
      </Spin>
    </Form>
  );
};

export default CreateItemYeuCauThanhToan;
